.duration-150 {
  transition-duration: 150ms;
}

.duration-300 {
  transition-duration: 300ms;
}

.duration-500 {
  transition-duration: 500ms;
}

html {
  font-size: 62.5%;
}

* {
  font-size: 1.8rem;
  font-weight: 500;
  font-family: 'Nunito Sans', sans-serif;
}

.preview__data {
  grid-template-columns: 1fr 2rem;
}

.recipe__fig::before {
  background-image: linear-gradient(to bottom right, #fbdb89,#f48982);
}

.recipe__fig span {
  background-image: linear-gradient(to bottom right, #fbdb89,#f48982);
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

.nav__btn--bookmarks:hover+.bookmarks {
  visibility: visible;
  opacity: 1;
}

header .preview__fig::before {
  content: "";
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(to right bottom,#fbdb89,#f48982);
  opacity: .4;
}

.before\:absolute::before {
  content: var(--tw-content);
  position: absolute;
}

.before\:left-0::before {
  content: var(--tw-content);
  left: 0px;
}

.before\:top-0::before {
  content: var(--tw-content);
  top: 0px;
}

.before\:h-full::before {
  content: var(--tw-content);
  height: 100%;
}

.before\:w-full::before {
  content: var(--tw-content);
  width: 100%;
}

.before\:opacity-70::before {
  content: var(--tw-content);
  opacity: 0.7;
}

.before\:content-\[\'\'\]::before {
  --tw-content: '';
  content: var(--tw-content);
}

.hover\:visible:hover {
  visibility: visible;
}

.hover\:-translate-y-\[1px\]:hover {
  --tw-translate-y: -1px;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.hover\:scale-105:hover {
  --tw-scale-x: 1.05;
  --tw-scale-y: 1.05;
  transform: translate(var(--tw-translate-x), var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
}

.hover\:bg-\[\#f2efee\]:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(242 239 238 / var(--tw-bg-opacity));
}

.hover\:bg-\[\#f9f5f3\]:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(249 245 243 / var(--tw-bg-opacity));
}

.hover\:bg-slate-hover:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(242 239 238 / var(--tw-bg-opacity));
}

.hover\:opacity-100:hover {
  opacity: 1;
}

.focus\:outline-none:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}
